"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  DocumentTextIcon,
  MagnifyingGlassIcon,
  ArrowPathIcon,
  ClockIcon,
  TagIcon,
  ChevronRightIcon,
  FolderIcon,
  ChartBarIcon,
  DocumentDuplicateIcon,
  UsersIcon,
  ShieldCheckIcon,
  ExclamationTriangleIcon,
  DocumentMagnifyingGlassIcon,
  ArchiveBoxIcon,
} from "@heroicons/react/24/outline";

/* =======================
   Types
======================= */

type RiskLevel = "HIGH" | "MEDIUM" | "LOW";

interface ComparisonItem {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
}

interface DocumentCard {
  id: string;
  name: string;
  risk: RiskLevel;
  latestVersion: string;
  lastUpdated: string;
  totalComparisons: number;
  category?: string;
}

/* =======================
   Config
======================= */

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() ||
  "http://127.0.0.1:8000";

const riskRank: Record<RiskLevel, number> = {
  HIGH: 3,
  MEDIUM: 2,
  LOW: 1,
};

function normalizeRisk(r?: string | null): RiskLevel {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleDateString("th-TH", {
      day: "numeric",
      month: "short",
      year: "numeric",
    });
  } catch {
    return iso;
  }
}

function extractCategory(name: string): string {
  const lowerName = name.toLowerCase();
  if (lowerName.includes("pdp") || lowerName.includes("data protection") || lowerName.includes("privacy")) {
    return "PDP";
  } else if (lowerName.includes("contract") || lowerName.includes("agreement") || lowerName.includes("‡∏™‡∏±‡∏ç‡∏ç‡∏≤")) {
    return "Contract";
  } else if (lowerName.includes("policy") || lowerName.includes("‡∏ô‡πÇ‡∏¢‡∏ö‡∏≤‡∏¢")) {
    return "Policy";
  } else if (lowerName.includes("report") || lowerName.includes("‡∏£‡∏≤‡∏¢‡∏á‡∏≤‡∏ô")) {
    return "Report";
  } else {
    return "General";
  }
}

function getCategoryIcon(category: string) {
  switch(category) {
    case "PDP":
      return <ShieldCheckIcon className="h-5 w-5 text-blue-500" />;
    case "Contract":
      return <DocumentDuplicateIcon className="h-5 w-5 text-purple-500" />;
    case "Policy":
      return <DocumentMagnifyingGlassIcon className="h-5 w-5 text-amber-500" />;
    case "Report":
      return <ChartBarIcon className="h-5 w-5 text-emerald-500" />;
    default:
      return <FolderIcon className="h-5 w-5 text-gray-500" />;
  }
}

function getCategoryColor(category: string) {
  switch(category) {
    case "PDP":
      return "bg-blue-50 border-blue-100";
    case "Contract":
      return "bg-purple-50 border-purple-100";
    case "Policy":
      return "bg-amber-50 border-amber-100";
    case "Report":
      return "bg-emerald-50 border-emerald-100";
    default:
      return "bg-gray-50 border-gray-100";
  }
}

/* =======================
   Page
======================= */

